import React, { Component, Fragment } from "react"
import { ApiGeneric } from "../../../../services/apiServices"
import { Collapse } from "reactstrap"
import styled from "styled-components"
import { MyModal, MyUI } from "src/core/components"

const WrapLayout = styled.div`
  & .group-api {
    margin-top: 2rem;
    & hr {
      margin: 0.5rem 0 0.5rem;
    }
  }
`
const APILayout = styled.div`
border: 1px solid ${props => props.color};
border-radius: 5px;
box-shadow: 0 0 3px rgb(0 0 0 / 19%);
background: ${props => props.color}1a;
margin-bottom: 1rem;
& .collapsing{
  transition: none;
}
& .api-header {
  cursor:pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid ${props => props.color};
  & .api-url {
    margin-bottom: 0;
    font-weight: 600;
    font-size: 1rem;
  }
  & button {
    background: ${props => props.color};
    color: #fff;
    font-weight: bold;
    font-size: 14px;
    padding: 6px 15px;
    border: 0;
    border-radius: 5px;
    min-width: 80px;
    margin-right: 1rem;
  }
}
& .api-request-header {
  background: #ffffffcc;
  display: flex;
  justify-content: space-between;
  padding: 8px 20px;
  align-items: center;
  & span {
    margin-bottom: 0;
    font-weight: 600;
    font-size: 1rem;
  }
  & button {
    border: 2px solid gray;
    font-size: 14px;
    font-weight: 700;
    padding: 5px 23px;
    border-radius: 5px;
    background: transparent;
  }
}
& .pre-wrap {
  background: #41444e;
  border-radius: 5px;
  & pre{
    padding: 10px;
    color: #fff;
    font-weight: 600;
    word-break: break-all;
    max-height: 500px;
    overflow-y: auto;
    font-size: 12px;
  }

}
& .api-request-body {
  padding: 20px;
  font-size: 12px;
  & h5{
    margin-top: 2rem;
    margin-bottom: 0;
    font-weight: 700;
  }
  & .api-request-table {
    width: 100%;
    & thead {
      border-bottom: 1px solid rgba(59, 65, 81, 0.2);
    }
    & td {
      padding: 12px 0;
      vertical-align: top;
    }
    &  th {
      padding: 6px 0;
      vertical-align: top;
    }
    & .parameter-field{
      & td{
        padding: 10px 5px;
      }
      font-size: 16px;
      background: #fff;
      & td{
        color: ${props => props.color};
        font-weight: 700;
        & span {
          font-size: 10px;
          color: rgba(255, 0, 0, 0.6);
          vertical-align: top;
        }
      }
      &:nth-child(even){
        background: #f5f5f5;
      }
    }
    & .reqeuest-field-title {
      & td{
        padding: 20px 0 0; 
      }
      h5 {
        font-size: 1rem;
        font-weight: 700;
        display: flex;
        align-items: top;
        margin-bottom: 5px;
      }
    }
    & .parameter-field-title{
      & td{
        padding: 0  0 5px;
      }
    }
  }
}
& .execute-wrapper {
  padding: 20px;
}
& .api-response-header {
  padding: 8px 20px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  & > span {
    font-size: 14px;
    font-weight: bold;
  }
}
& .api-response-body {
  padding: 20px;
    & p{
      margin: 10px 0 5px;
      font-size: 12px;
      font-weight: 700;
    }
  }
  & .api-response-table{
    width: 100%;
    table-layout: fixed
  }
  & th{
    font-size: 14px;
  }
  & thead {
    border-bottom: 1px solid rgba(59, 65, 81, 0.2);
  }
  & td{
    padding: 10px 0 5px;
  }
  & .api-response-code{
    width: 400px;
    min-width: 400px;
    vertical-align: top;
    & p{
      margin: 0;
    }
    & p:last-child{
      color: gray;
      font-style: italic;
    }
  }
  & .api-response-content{
    & h5{
      font-size: 16px;
    }
    & p{
      margin: 10px 0 5px;
    }
  }
}
`

class MP_API_Document extends Component {
  constructor(props) {
    super(props)
    this.state = {
      Data: [],
      open: true,
    }
  }
  componentDidMount() {
    ApiGeneric({
      request: {
        method: "POST",
        path: "/api/v1/ApiDocument_Demo",
        name: "List",
      },
      data: {},
      successCallBack: response => {
        if (response.Data.Data) {
          this.setState({ Data: response.Data.Data })
        }
      },
      errorCallBack: err => {
        console.log(err)
      },
    })
  }

  renderListApi = () => {
    const objGroup = this.state.Data.reduce((pre, cur) => {
      pre[cur.GroupName] = pre[cur.GroupName] || []
      pre[cur.GroupName].push(cur)
      return pre
    }, {})
    const renderGroup = []
    for (let GroupName in objGroup) {
      renderGroup.push(
        <div className="group-api" key={GroupName}>
          <h5>{GroupName}</h5>
          <hr />
          {objGroup[GroupName]?.map((api, index) => {
            api.Response = api.Response ? api.Response : JSON.stringify({})
            api.Authorization = api.Authorization
              ? api.Authorization
              : JSON.stringify({})
            const color =
              api.Method === "GET"
                ? "#49cc90"
                : api.Method === "POST"
                ? "#61affe"
                : api.Method === "PUT"
                ? "#fca130"
                : "#f93e3e"
            const renderTable = []
            const renderParameters = []
            const rederHeaders = []
            renderTable.push(
              <Fragment>
                <h5>Headers</h5>
                <table className="api-request-table">
                  <colgroup>
                    <col style={{ width: "50%" }} />
                    <col style={{ width: "50%" }} />
                  </colgroup>
                  <thead>
                    <tr>
                      <th>Key</th>
                      <th>Value</th>
                    </tr>
                  </thead>
                  <tbody>{rederHeaders}</tbody>
                </table>
              </Fragment>
            )
            renderTable.push(
              <Fragment>
                <h5>Parameters</h5>
                <table className="api-request-table">
                  <colgroup>
                    <col style={{ width: "50%" }} />
                    <col style={{ width: "50%" }} />
                  </colgroup>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Description</th>
                    </tr>
                  </thead>
                  <tbody>{renderParameters}</tbody>
                </table>
              </Fragment>
            )
            for (let fieldName in api) {
              if (
                fieldName === "Headers" ||
                fieldName === "Authorization" ||
                fieldName === "Parameters"
              ) {
                const render = []
                const object = api[fieldName] && JSON.parse(api[fieldName])
                for (let field in object) {
                  render.push(
                    <tr className="parameter-field" key={field}>
                      <td>
                        {field}
                        {object[field].required ? (
                          <span>&nbsp;* required</span>
                        ) : (
                          ""
                        )}
                      </td>
                      <td>{object[field].type}</td>
                    </tr>
                  )
                }
                if (fieldName === "Headers" || fieldName === "Authorization") {
                  rederHeaders.push(render)
                } else {
                  renderParameters.push(render)
                }
              }
            }
            return (
              <APILayout color={color} key={index}>
                <div
                  className="api-header"
                  onClick={() => {
                    this.setState({
                      [`${GroupName + index}`]:
                        !this.state[`${GroupName + index}`],
                    })
                  }}
                >
                  <button>{api.Method}</button>
                  <div className="api-url">{api.Url}</div>
                </div>
                <Collapse isOpen={this.state[`${GroupName + index}`]}>
                  <div className="api-request">
                    <div className="api-request-header">
                      <span>Request</span>
                      <button
                        onClick={() => {
                          MyModal.showFromMyPage(
                            {
                              UIType: "api_request",
                            },
                            {
                              title: api.Url,
                              size: "xl",
                              fnList: {
                                getDefautR: () => {
                                  return api
                                },
                              },
                              data: api,
                            }
                          )
                          // MyModal.showFromComponent(
                          //   <MyUI type="api_client" data={api} />,
                          //   {
                          //     modalCentered: true,
                          //     showHeader: true,
                          //     title: api.Url,
                          //     size: "xl",
                          //     onClickOK: cModal => {
                          //       if (cModal.hide) {
                          //         cModal.hide()
                          //       }
                          //     },
                          //   }
                          // )
                        }}
                      >
                        Try it out
                      </button>
                    </div>
                    <div className="api-request-body">{renderTable}</div>
                    <div className="execute-wrapper"></div>
                  </div>
                  <div className="api-response">
                    <div className="api-response-header">
                      <span>Responses</span>
                    </div>
                    <div className="api-response-body">
                      <p>Server response</p>
                      <table className="api-response-table">
                        <colgroup>
                          <col style={{ width: "30%" }} />
                          <col style={{ width: "70%" }} />
                        </colgroup>
                        <thead>
                          <tr>
                            <th>Code</th>
                            <th>Details</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td className="api-response-code">
                              <p>200</p>
                              <p>Undocumented</p>
                            </td>
                            <td className="api-response-content">
                              <h5>Success</h5>
                              <p>Response headers</p>
                              <div className="pre-wrap">
                                <pre>
                                  {JSON.stringify(
                                    JSON.parse(api.Response),
                                    null,
                                    2
                                  )}
                                </pre>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </Collapse>
              </APILayout>
            )
          })}
        </div>
      )
    }
    return renderGroup
  }

  render() {
    console.log("MP_API_Document")
    return <WrapLayout>{this.renderListApi()}</WrapLayout>
  }
}

export default MP_API_Document
